import React, { useState } from 'react';
import faker from 'faker';
import { data } from './data'

const BirthdayCard = () => {
    //set array and method
    //pass to useState (data) as default value
    const [people, setPeople] = useState(data);

    return (
        <div style={{width: "400px",backgroundColor: "white", padding: "20px", marginTop: "50px", borderRadius: "5px", boxShadow: "-9px 5px 42px -17px rgba(0,0,0,0.75)"
    }}>
            <h1>{`${people.length} birthdays today`}</h1>
            {people.map((person) => {
                const {id, name, age} = person
                return (
                    <div key={id} className="ui items">
                        <div className="item">
                            <img className="ui tiny image" src={faker.image.avatar()} alt="avatar"/>
                        <div style={{textAlign: "left"}} className="middle aligned content">
                            <div className="header">
                                <h2>{name}</h2>
                                <i className="birthday cake yellow icon"></i>
                                <span>{age}</span>
                            </div>
                        </div>
                        </div>
                    </div>
                )
            })
            }{people.length > 0 ? 
            <RemoveButton onClick={()=>setPeople([])}/> : 
            <RestoreButton onClick={()=>setPeople(data)}/>
            }
        </div>
    )
}

const RemoveButton = (props) => {
    return (
        <button onClick={props.onClick} className="fluid ui button red">
        <i className="minus circle icon"></i>
        Clear All
    </button>
    )
}

const RestoreButton = (props) => {
    return (
        <button onClick={props.onClick} className="fluid ui button green">
        <i className="plus circle icon"></i>
        Restore All
    </button>
    )
}

export default BirthdayCard